import React from 'react';

const MetadataContext = React.createContext(undefined);


export const MetadataProvider = ({ children }) => {
    const [md, setmd] = React.useState(undefined);

    React.useEffect(() => {
        fetch('Nation-Origins-α.json')
            .then(response => {
                response.json().then(data => {
                    setmd(data)
                })
            })
    }, [])

    return (
        <MetadataContext.Provider value={md}>
            {children}
        </MetadataContext.Provider>
    )
}

export const useMetadata = (collectionName) => {
    return React.useContext(MetadataContext);
}