import React from 'react';


export const DotLoading = ({className}) => {
    const [n, setn] = React.useState(1);

    React.useEffect(() => {
        const interval = setInterval(() => {
            setn(n + 1);
        }, 1000);
        return () => clearInterval(interval);
    }, [n])


    return (
        <div className={className}>
            <p>{'·'.repeat(n % 3 + 1)}</p>
        </div>
    )
}
