import './Home.css';


export const HomeView = () => {
    const md = useMetadata();
    const [loading, setloading] = React.useState(true);

    React.useEffect(() => {
        if (md) {
            setloading(false);
            return
        }

        setloading(true);
    }, [md, setloading])

    return (
        <div className="home">
            {loading || <nav className="home-nav">
                <a target="__blank" href="https://zhuanlan.zhihu.com/p/368736357">教程</a>
                <a target="__blank" href="https://jinshuju.net/f/jM0Nu1">Airdrop</a>
                <Link className="explore-btn" to='/collection/Nation-Origins-α'>探索α</Link>
            </nav>}

            <div className="logo-box">
                <h1 className="title">Nation Origins</h1>
            </div>

            <div className="cards">{HomeCards.map((item, i) => <Card key={i} showMeta={false} item={item} />)}</div>
            {loading && <DotLoading className="home-loading" />}

        </div>
    )
}


