import React from 'react';
import { useWeb3 } from './Web3';
import NationContract from '../contracts/Nation.json';
import { BSCMainNetRPCHelper } from '../components/helper/BscRPCHelper';

const NationContractContext = React.createContext(undefined);


export const NationContractProvider = ({ children }) => {

    const {web3, chainId} = useWeb3();

    const [instance, setinstance] = React.useState(undefined);
    const [isBsc, setisBsc] = React.useState(true);
    React.useEffect(() => {
        if (!web3 || !chainId) { return }
        async function createInstance() {
            setisBsc(chainId == 97 || chainId == 56 || chainId == 1337);

            
            const deployedNetwork = NationContract.networks[chainId];
            console.log(deployedNetwork.address)
            const instance = new web3.eth.Contract(
                NationContract.abi,
                deployedNetwork && deployedNetwork.address,
            );
            setinstance(instance)
        }

        createInstance()
    }, [web3, chainId])


    return (
        <NationContractContext.Provider value={instance}>
            {
                isBsc
                    ?
                    children
                    :
                    <BSCMainNetRPCHelper />
            }
        </NationContractContext.Provider>
    )
}

export const useNationContract = () => {
    return React.useContext(NationContractContext);
}