import React from 'react';
import { useNationContract } from '../../providers/NationContract';
import ContentLoader from "react-content-loader"
import Jdenticon from 'react-jdenticon';
import { AddressText } from '../AddressText/AddressText';

import './Card.css';

const SPECIAL_ATTRIBUTE = {
  '无政府主义': 1,
  '脑机': 1,
  '飞碟残骸': 1,
  '幸运': 1,
  '比特币': 1,
  '太空旅行': 1,
  '熔岩地貌': 1,
  '风蚀地貌': 1,
  '冰川地貌': 1,
};

function isSpecialCard(item) {
  return item.attributes.some((attr) => {
    return SPECIAL_ATTRIBUTE[attr.value] > 0
  })
}

export const Card = ({ item, showMeta=true }) => {
  const contract = useNationContract();
  const [ownerAddress, setownerAddress] = React.useState(undefined);

  const isSpecial = React.useMemo(() => {
    return isSpecialCard(item)
  }, [item])

  React.useEffect(() => {

    async function getToken() {
      if (!contract) {
        console.log('contract not inited');
        return
      }

      try {
        const owner = await contract.methods.ownerOf(item.token_id).call();
        setownerAddress(owner);
      } catch (e) {

      }

    }

    getToken()
  }, [contract, item])


  return (
    <>
      {
        item &&

        <div className="card">
          <img src={item.image} alt={item.name} />
          {showMeta && <div className="card-meta">
            <h2 className="item-title">{item.description}</h2>
            <p className="item-desc">{item.name}</p>
            {
              ownerAddress
                ?
                <Owner address={ownerAddress} />
                : <OwnerLoader />
            }

          </div>}
          {/* {isSpecial && <img className="special" src="fire.gif" alt="special" />} */}
        </div>
      }

    </>
  )
}

const Owner = (props) => {
  const { address } = props;
  return (
    <div className="owner-box">
      <Jdenticon size="48" value={address} />
      <div className="owner-address">
        <AddressText style={{ fontSize: 15}} address={address} />
      </div>
    </div>

  )
}

const OwnerLoader = (props) => {
  return (
    <ContentLoader
      speed={2}
      width={400}
      height={50}
      viewBox="0 0 400 50"
      backgroundColor="#f3f3f3"
      foregroundColor="#ecebeb"
      {...props}
    >
      <rect x="68" y="30" rx="3" ry="3" width="88" height="15" />
      <rect x="68" y="10" rx="3" ry="3" width="188" height="15" />
      <rect x="0" y="5" rx="3" ry="3" height="48" width="48"/>
    </ContentLoader>
  )
}