import React from 'react';
import Web3 from 'web3';

const Web3Context = React.createContext({});


async function getWeb3() {
    if (window.ethereum) {
        const web3 = new Web3(window.ethereum);
        try {
            // Request account access if needed
            await window.ethereum.enable();
            // Accounts now exposed

            return web3
        } catch (error) {
            console.log(error);
        }
    }
    // Legacy dapp browsers...
    else if (window.web3) {
        // Use Mist/MetaMask's provider.
        const web3 = window.web3;
        console.log("Injected web3 detected.");
        return web3
    }
    // Fallback to localhost; use dev console port by default...
    else {
        const provider = new Web3.providers.HttpProvider(
            "http://127.0.0.1:8545"
        );
        const web3 = new Web3(provider);
        console.log("No web3 instance injected, using Local web3.");
        return web3
    }
}


export const Web3Provider = ({ children }) => {
    const [web3, setweb3] = React.useState(undefined);
    const [account, setaccount] = React.useState(undefined);
    const [chainId, setchainId] = React.useState(undefined);


    const disconnect = React.useCallback(() => {
        if (web3) {
            setweb3(undefined);
            setaccount(undefined);
        }
    }, [setweb3, web3])

    const connect = React.useCallback(async () => {
        if (web3) { return }
        window.ethereum.on('accountsChanged', (accounts) => setaccount(accounts[0] || undefined));
        window.ethereum.on('chainChanged', (chainId) => setchainId(chainId));
        
        const instance = await getWeb3();
        const accounts = await instance.eth.getAccounts();
        if (accounts.length > 0) {
            setaccount(accounts[0])
        }


        setweb3(instance);
        setchainId(await instance.eth.net.getId());

    }, [web3, setweb3, setaccount])

    return (
        <>
            <Web3Context.Provider value={{ chainId, web3, connect, disconnect, account }}>
                {children}
            </Web3Context.Provider>
        </>
    )
}

export const useWeb3 = () => {
    return React.useContext(Web3Context);
}